import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import http from '@/http/interface.js'
export default {
	components: {cmdNavBar},
	data() {
		return {
			missVal: '',
			missShow : false,
			//股票信息
			stockInfo : {},
			//用户可用余额
			available : 0,
			//用户输入买入价格
			buyPrice : '',
			//用户输入买入个数
			buyNum : '',
			//用户收藏的股票数据
			stocks : [],
			//股票搜索框提示
			stocksWant : [],
			//持有股票数据
			buysData : [],
			//最大可卖
			maxSale : 0
		};
	},
	methods: {
		treChange(evt){
			this.buyPrice = this.stockInfo.curPrice;
		},
		buyInChange(evt){
			this.buyNum = (this.maxSale/evt.target.value).toFixed(0)
		},
		//刷新
		refresh(){
			if(this.missVal.length == 6){
				this.queryStock(this.missVal)
			}
		},
		//删除或添加
		priceDelAdd(type){
			if(type == 'del'){
				this.buyPrice = (Number(this.buyPrice) - 0.01).toFixed(2);
			}else if(type == 'add'){
				this.buyPrice = (Number(this.buyPrice) + 0.01).toFixed(2);
			}
		},
		numDelAdd(type){
			if(type == 'del'){
				this.buyNum = (Number(this.buyNum) - 100);
			}else if(type == 'add'){
				this.buyNum = (Number(this.buyNum) + 100);
			}
		},
		//搜索键入监听
		bitListen(evt){
			let val = evt.target.value;
			let arr = [];
			this.stocks.forEach((el,index)=>{
				if(el.stockCode.indexOf(val) != -1){
					arr.push(el)
				}
			})
			arr = arr.slice(0,5);
			this.stocksWant = arr;
			if(this.stocksWant.length > 0){
				this.missShow = true;
			}else{
				this.missShow = false;
			}
			if(val.length == 6){
				this.queryStock(val)
			}
		},
		//价格 数量 脱焦监听
		priceCheck(evt){
			this.buyPrice = Number(this.buyPrice).toFixed(2);
			if(evt.target.value < 0){
				uni.showToast({
					position: 'bottom',
					title: '卖出价格不能小于0',
					icon: 'none',
					duration: 2500
				});
				return
			}
		},
		numCheck(evt){
			if(evt.target.value%100 != 0 || Number(evt.target.value) <= 0){
				uni.showToast({
					position: 'bottom',
					title: '数量只能是100的倍数！',
					icon: 'none',
					duration: 2500
				});
				return
			}
			if(evt.target.value > this.maxSale){
				uni.showToast({
					position: 'bottom',
					title: '不能超过最大可卖数！',
					icon: 'none',
					duration: 2500
				});
				return
			}
		},
		//提示框选中
		selectThis(stockCode){
			this.missShow = false;
			this.queryStock(stockCode);
		},
		//查询股票
		queryStock(stockCode){
			
			let isgo = false;
			let _this = this;
			this.buysData.forEach((el)=>{
				if(el.stockCode == stockCode){
					isgo = true;
					_this.maxSale = el.stockAmount;
					return;
				}
			})
			
			if(isgo){
				http.get('transaction/tobuy',{stockCode:stockCode,phone:this.$store.state.userInfo.phone}).then((res)=>{
					if(res.data.data.stockMap != undefined){
						this.stockInfo = res.data.data.stockMap;
						this.missVal = this.stockInfo.stockCode;
						this.buyPrice = this.stockInfo.curPrice;
						this.available = res.data.data.balance.available;
					}
				})
			}else{
				uni.showModal({
					title : '提示',
					content : '您当前未持有该股票',
					showCancel : false,
				})
			}
		},
		//获取持有股票
		getHasStock(){
			http.get('transaction/tosell',{phone:this.$store.state.userInfo.phone}).then((res)=>{
				console.log(res);
				this.buysData = res.data.data;
				this.stocks = res.data.data;
			})
		},
		//提交
		immediatelyBuy(){
			if(this.stockInfo.stockCode == undefined || this.stockInfo.stockCode == ''){
				uni.showModal({
					title : '提示',
					content : '请选择您要卖出的股票',
					showCancel : false,
				})
				return;
			}
			if(Number(this.buyPrice) < 0 || Number(this.buyPrice) == NaN || this.buyPrice == ''){
				uni.showModal({
					title : '提示',
					content : '请输入正确的卖出价',
					showCancel : false,
				})
				return;
			}
			if(this.buyNum%100 != 0 || Number(this.buyNum) <= 0 || this.buyNum == ''){
				uni.showModal({
					title : '提示',
					content : '数量只能是100的倍数！',
					showCancel : false,
				})
				return
			}
			if(this.buyNum > this.maxSale){
				uni.showModal({
					title : '提示',
					content : '不能超过最大可卖数！',
					showCancel : false,
				})
				return
			}
			let _this = this;
			uni.showModal({
				title: '请确认',
				content: '是否卖出【'+this.stockInfo.stockName+'】,代码【'+this.stockInfo.stockCode+'】,卖出价【'+this.buyPrice+'】,卖出数量【'+this.buyNum+'】',
				success: function (res) {
					if (res.confirm) {
						uni.showLoading({
							title: '提交中~'
						});
						http.get('transaction/sell?stockCode='+_this.stockInfo.stockCode+'&holder='+_this.$store.state.userInfo.phone+'&price='+_this.buyPrice+'&amount='+_this.buyNum).then((res)=>{
							if(res.data.success){
								uni.showModal({
									title : '提示',
									content : res.data.message,
									showCancel : false,
								})
								_this.getHasStock('');
							}
						})
					} else if (res.cancel) {
						
					}
				}
			});
		},
		//选中
		cancellation(el,index){
			this.queryStock(el.stockCode);
			this.maxSale = el.stockAmount;
		}
	},
	onLoad(options) {
		uni.showLoading({
			mask:true
		})
		this.getHasStock();
	},
	mounted(){
		
	}
}
